import React from 'react'
import ReactAudioPlayer from 'react-audio-player'

import {
	Collapse,
	Navbar,
	NavbarToggler,
	NavbarBrand,
	NavItem,
	NavLink,
} from 'reactstrap';


export default class NavigationMenu extends React.Component {
	constructor(props) {
		super(props);

		this.toggle = this.toggle.bind(this);
		this.state = {
			isOpen: false
		};
	}
	
	toggle() {
		if(window.innerWidth<768) {
			this.setState({
				isOpen: !this.state.isOpen
			});
		}
	}

	render() {
		return (
			<>
				<Navbar className="fixed-top" dark expand="md">
					<div className="container">
						<NavbarBrand href="/" className="d-block d-md-none mr-auto">AG</NavbarBrand>
						<NavbarToggler onClick={this.toggle} className="mr-2" aria-label="Ouvrir/Fermer le menu navigation" />
						<Collapse isOpen={this.state.isOpen} navbar>
							<ul className="mr-auto navbar-nav" items={['presentation', 'formation', 'competences', 'experiences', 'realisations', 'contact']} currentClassName="active">
								<NavItem onClick={this.toggle}>
									<NavLink href={process.env.siteUrl+"#presentation"} aria-label="Accueil">
										<i className="fa fa-home" aria-hidden="true"></i>
									</NavLink>
								</NavItem>
								<NavItem onClick={this.toggle}>
									<NavLink href={process.env.siteUrl+"#formation"} >Formation</NavLink>
								</NavItem>
								<NavItem>
									<NavLink onClick={this.toggle} href={process.env.siteUrl+"#competences"} >Compétences</NavLink>
								</NavItem>
								<NavItem onClick={this.toggle}>
									<NavLink href={process.env.siteUrl+"#experiences"} >Expériences</NavLink>
								</NavItem>
								<NavItem onClick={this.toggle}>
									<NavLink href={process.env.siteUrl+"#realisations"} >Réalisations</NavLink>
								</NavItem>
								<NavItem onClick={this.toggle}>
									<NavLink href={process.env.siteUrl+"#contact"} >Contact</NavLink>
								</NavItem>
							</ul>
						</Collapse>
					</div>
				</Navbar>
				<ReactAudioPlayer
					id="audio-player"
					className="audio-player"
					src="ever-felt-pt2-otis-mcdonald.mp3"
					autoPlay
					controls
				/>
		  </>
		);
	}
}